<template>
	<div class="wallet">
		<div style="text-align: center;padding-top: 120px;">
			<img src="../../assets/img/logo.png" width="100"/>
			<h2 style="color:#4372F6;margin-bottom: 0px;">币多多生态钱包</h2>
			<p style="color:#4372F6;">Multi-currency Eco-wallet</p>
		</div>
		<div class="buttons">
			<div>
				<router-link to="/creatWallet"><mu-button style="height: 44px;" class="creatBtn" color="#295DF1">创建钱包</mu-button></router-link>
			</div>
			<div style="margin-top: 20px;">
				<router-link to="/importWallet"><mu-button flat class="importBtn">导入钱包</mu-button></router-link>
			</div>
		</div>
		
		<mu-paper style="position: fixed;bottom: 50px;right: 20px;width: 40px;height: 40px;text-align: center;" @click="goBackWebview" class="demo-paper" circle :z-depth="5">
	        <!--<span class="icon iconfont" style="font-size: 26px;color:black;">&#xe647;</span>-->
	    	<p style="font-size: 12px;margin-top:8px;"><<返回App</p>
	    </mu-paper>
	</div>
</template>

<script>
export default {
  name: 'Wallet',
  data(){
  	return{
  		
  	}
  },
  mounted(){
  	
  },
  methods:{
  	goBackWebview(){
  		if (!window.plus) return;
  		plus.webview.currentWebview().close(); 
  	}
  },
  computed:{
  	
  },
 }
</script>

<style scoped>
.wallet {
	height: 100%;
	background-image: url(../../assets/img/bg.png);
	background-repeat: no-repeat;
	background-size: 100% 100%;
}
.buttons{
	position: absolute;
	width: 80%;
	left: 10%;
	bottom: 25%;
}
.creatBtn{
	width: 100%;
}
.importBtn{
	width: 100%;
	height: 44px;
	color: white;
	border: 1px solid #295DF1;
}
</style>


